	module.exports = {"data":{"markdownRemark":{"html":"<p>Adding a 404 page is easy. Simply create a page whose path matches the regex <code>/404*</code>. Most often you’ll just want to create a React component page at <code>src/pages/404.js</code>.</p>\n<p>Gatsby ensures that your 404 page is built as <code>404.html</code> as many static hosting platforms default to using this as your 404 error page. If you’re hosting your site another way, you’ll need to setup a custom rule to serve this file for 404 errors.</p>\n<p>When developing, Gatsby adds a default 404 page that overrides your custom 404 page. But you can still visit the exact url for your 404 page to verify it’s working as expected.</p>","excerpt":"Adding a 404 page is easy. Simply create a page whose path matches the regex  /404* . Most often you’ll just want to create a React…","timeToRead":1,"frontmatter":{"title":"Add 404 Page"},"parent":{"__typename":"File","relativePath":"docs/add-404-page.md"}}},"pathContext":{"slug":"/docs/add-404-page/"}}

